@import url('https://rsms.me/inter/inter.css');

* {
    font-family: 'Inter var', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #000000;
    color: #cbcbcb;
    box-sizing: border-box;
  }


a {
    transition: all 0.3s ease 0s;
}

/* Navbar style */

@media screen and (max-width: 900px) {
    .cta {
      display: none;
    }
    .nav-links a {
        font-size: 13px;
    }
    .logo {
        font-size: 13px;
    }
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 5%;
    border-bottom: 0.1px solid #202020;
}

.nav-links {
    list-style: none;
}

.nav-links li, a, button {
    font-size: 16px;
    text-decoration: none;
}

.nav-links li {
    display: inline-block;
    padding: 0px 20px;
}

.nav-links li a {
    transition: all 0.3s ease 0s;
}

.nav-links li a:hover {
    color: #ededed;
}

.cta button {
    padding: 9px 25px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}

.cta button:hover {
    color: #fff;
}

.logo {
    transition: 0.2s;
}
.logo:hover {
    cursor: pointer;
    color: #fff;
}

/* home style */
@media screen and (max-width: 900px) {
    .home-text .title {
        margin: 100px;
        font-size: 15px;
        font-weight: 300;
    }
}

@media screen and (min-width: 900px) {
    .home-text .title {
        margin: 100px;
        font-size: 20px;
        font-weight: 300;
    }
}

.home {
    text-align: center;
    width: 100%;
    height: 100%;
}

.home-text .discraption a {
    color: #828282;
}
.home-text .discraption a:hover {
    color: #ffffff;
}

.home-text .home-logo img {
    max-width: 400px;
}
.BgAnimation__svg {
    margin-top: 100px;
    max-width: 500px;
}